<script>
	import { mapActions, mapGetters } from "vuex";
import {Col, Row,NavBar,Cell, CellGroup,Button,Overlay,Swipe, SwipeItem,Popup,Image,ImagePreview} from "vant";
import {houseDetail,houseContract,houseShareQrcode} from "@/api/v4.js"
import buttons from "@/components/button"
import html2canvas from "html2canvas";
import { setShare } from "../../wechat";
import { PAGE_BASE_URL } from "../../config";
export default{
	components:{
		[NavBar.name]: NavBar,
		[Col.name]: Col,
		[Row.name]: Row,
		[Cell.name]: Cell,
		[CellGroup.name]: CellGroup,
		[Button.name]: Button,
		[Overlay.name]: Overlay,
		[Swipe.name]: Swipe,
		[SwipeItem.name]: SwipeItem,
		[Popup.name]: Popup,
		[Image.name]: Image,
		buttons
	},
	data(){
		return{
			show:false,
			details:[],
			house_id:null,
			dialogTableVisible:false,
			posterImg:"",
			share_ewm:"",
			shaerImgs:null
		}
	},
	created() {
		let house_id =  this.$route.params.id
		this.house_id = house_id
		houseDetail({house_id}).then(res=>{
			this.details = res
			this.shaerImgs = res.images[0]
			const desc = `${
			  this.details.title
			}。点击查看房源详情`;
			const link = PAGE_BASE_URL + "#/workshop/details/" + this.details.house_id;
			setTimeout(() => {
			  setShare({
			    title: "天集社", // 分享标题
			    desc,
			    link
			  });
			}, 1000);
		})
		houseShareQrcode({house_id}).then(res => {
		  this.share_ewm = res.url;
		});
		
	},
	methods:{
		goBack() {
			this.$router.back();
		},
		clickWorkshop(){
			if(this.userInfo.status == 2){
				this.show = true
				houseContract({house_id:this.house_id}).then(res=>{
					console.log(res)
				})
			}else{
				this.$dialog
				  .alert({
				    title: "温馨提示",
				    message: `请先注册`,
				    confirmButtonText: "确认"
				  }).then(res=>{
					  this.$router.push("/my-card")
				  })
			}
			
		},
		hideYY(){
			this.dialogTableVisible = false
		},
		close(){
			this.dialogTableVisible = false
		},
		share(){
			if(this.userInfo.status == 2){
				this.show = false
				// 生成海报
				window.pageYOffset = 0;
				document.documentElement.scrollTop = 0
				document.body.scrollTop = 0
				var copyDom = this.$refs.posterHtml; //要保存的dom
				var scale = 2; //放大倍数
				html2canvas(copyDom, {
				  async: true,
				  useCORS: true,
				  allowTaint: true,
				  dpi: 326,
				  scale: 2,
				  taintTest: true,
				}).then(canvas => {
				  let dataURL = canvas
				    .toDataURL("image/png")
				    .replace("image/png", "image/octet-stream");
				  this.posterImg = dataURL;
				  if (this.posterImg !== "") {
				    this.dialogTableVisible = true;
				  }
				});
			}else{
		this.$dialog
		  .alert({
		    title: "温馨提示",
		    message: `请先注册`,
		    confirmButtonText: "确认"
		  }).then(res=>{
			  this.$router.push("/my-card")
		  })
	}
			
		},
		showImg(index){
			ImagePreview({
			  images: this.details.images,
			  startPosition: index,
			  lazyLoad: true,
			  onClose() {
			    // do something
			    console.log("close image preview");
			  }
			});
		},
		goHome(){
			this.$router.push("/workshop/index")
		}
	},
	computed: {
	  ...mapGetters(["userInfo"])
	},
}
</script>

